import {mapState, mapMutations} from 'vuex';

export default {
	data() {
		return {
			account: '',
			password: ''
		}
	},
	methods: {
		...mapMutations(['login']),
		bindLogin: function() {
			this.$http.request({
				url: '/music/getDetail',
				method: 'post',
				params: {
					id: 3
				}
			}).then(res => {
				console.log(res)
				
				var userinfo = {
					username: this.account,
					password: 'www',
					userImg: './static/logo.png'
				}
				if (this.password === userinfo.password) {
					this.toLogin(userinfo)
				} else{
					uni.showToast({
						icon: 'none',
						title: '用户账号或密码不正确',
					});
				}
				
			}).catch(err => {
				console.log(err)
				
				// 模拟数据
				var userinfo = {
					username: this.account,
					password: 'www',
					userImg: './static/logo.png'
				}
				if (this.password === userinfo.password) {
					this.toLogin(userinfo)
				} else{
					uni.showToast({
						icon: 'none',
						title: '用户账号或密码不正确',
					});
				}
			})
		},
		toLogin: function(userinfo) {
			this.login(userinfo)
			uni.reLaunch({
				url: '../index/index',
			});
		}
	}
}
